	import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"


	//高德SDK
	import amap from '../../common/SDK/amap-wx.js';
	export default {
		components: {
			uniLoadMore
		},
		mounted() {
			
			this.init();

			uni.hideLoading();
			//系统消息
			this.request({
				url: this.config.url + "member/message",
				method: "POST",
				data: {
					token: this.token
				},
				success: (res) => {
					

					if (res.data.code == 1) {
						this.msgList = res.data.data.data
					}
				}
			})

		},

		data() {
			return {
				msg: "", //系统消息显示
				msgList: [], //系统信息列表
				showIcon: false,
				status: "more",
				afterHeaderOpacity: 1, //不透明度
				headerPosition: 'fixed',
				headerTop: null,
				statusTop: null,
				city: '北京',
				currentSwiper: 0,
				// 轮播图片
				swiperList: [],
				// 分类菜单
				categoryList: [],
				Promotion: [],
				// 热销产品
				hotList: [],
				//限制产品
				limitList: [],
				//猜你喜欢列表
				productList: [],
				totalList: [],
				loadingText: '正在加载...',
				token1: "",
                sendUrl:"",  //寄件url
				current_page: 1,
				total: "",
				last_page: "1",
				tishi: false,
				hot: [],
				limit: [],
				home: false
			}
		},
		//分享页面 
		onShareAppMessage: function() {
			var that = this;
			return {
				title: '快来看一看啊！',
				path: '/pages/tabBar/home'
			}
		},

		onPageScroll(e) {
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
			this.headerTop = e.scrollTop >= 0 ? null : 0;
			this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {

			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
			this.current_page = 1
			this.productList = [];
			this.hotList = []
			this.limitList = []
			this.pulldown()

		},
		//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
		onReachBottom() {
			// 调用获取推荐列表接口
			// 当前页小于最后一页才调用
			this.current_page++
			if (this.current_page > this.last_page) {
				return;
			} else {
				this.getRecommendList();
			}

		},
	
		onLoad() {

			this.pulldown()
		},
		methods: {
			//下拉加载
			pulldown() {

				uni.showLoading({
					title: '加载中'
				});
				uni.getStorage({
					key: "info",
					success: (res) => {

						var result = JSON.parse(res.data)

						this.token1 = result.token;
						this.first_load();
						this.getRecommendList()
					}
				})
			},
			//加载更多热销产品
			moreLoad() {
				uni.navigateTo({
					url: "/pages/goods/goods-list?hot=hot"
				})

			},
			//加载限时购商品
			moreLimit() {
				uni.navigateTo({
					url: "/pages/goods/goods-list?limit=limit"
				})

			},
			init() {
				uni.hideLoading();


			},
			first_load() {


				var amapPlugin = new amap.AMapWX({
					//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
					key: '5b9b64be2413fc19c26683fcf0de890f'
				});
				//定位地址
				amapPlugin.getRegeo({
					success: data => {
						//console.log (data,"位置")
						this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉
						this.init()
					},
					fail: data => {
						//console.log(data,"位置")
					}
				});

				//系统消息
				this.request({
					url: this.config.url + "member/message",
					method: "POST",
					data: {
						token: this.token1
					},
					success: (res) => {
						//console.log("mesg",res)

						if (res.data.code == 1) {
							this.msgList = res.data.data.data;
							if (res.data.data.data.length > 0) {
								this.msg = true;
							}
						}
					}
				})
				//主页数据
				this.request({
					url: this.config.url + 'home', //仅为示例，并非真实接口地址。
					data: {

					},
					method: "post",
					success: (res) => {
						//console.log("home", res);
						var num, len;
						if (res.data.code == 1) {
							//console.log(res)
							this.swiperList = res.data.data.banner;
							this.categoryList = res.data.data.cate;
                            this.sendUrl=res.data.data.jijian.cover;
							this.limit = res.data.data.limit_buy;
							this.hot = res.data.data.hot;
							this.home = true;
							if (this.limit.length < 4) {
								len = this.limit.length;
							} else {
								len = 4;
							}
							for (var i = 0; i < len; i++) {
								this.limitList.push(this.limit[i])

							}

							if (this.hot.length < 6) {
								num = this.hot.length;
							} else {
								num = 6;
							}
							for (var i = 0; i < num; i++) {
								this.hotList.push(this.hot[i])
								//	console.log(this.productList)
							}

						}

					}
				})


			},


			// 获取推荐列表
			getRecommendList() {
				this.status = "loading";
				// 首页为你推荐
				this.request({
					url: this.config.url + 'recommend', //仅为示例，并非真实接口地址。
					data: {
						token: this.token1,
						page: this.current_page,
					},
					method: "post",
					success: (res) => {
						var len;
						//console.log(this.current_page,"res.data", res.data);
						// 商品列表
						this.totalList = res.data.data.data;
						var page = res.data.data.current_page;
						//每页10 
						if (page == 1) {
							if (this.totalList.length < 10) {
								len = this.totalList.length;
							} else {
								len = 10;
							}
							for (var i = 0; i < len; i++) {
								this.productList.push(this.totalList[i])
								//	console.log(this.productList)
							}
						} else {
							this.productList.concat(res.data.data.data)
						}



						this.last_page = res.data.data.last_page;
						this.total = res.data.data.data.total;
						this.status = "more";
						if (this.current_page >= this.last_page) {
							this.status = "noMore";
						}
					}
				});

			},


			//消息列表
			toMsg() {
				uni.navigateTo({
					url: '/pages/msg/msg'
				})
			},
			//搜索跳转
			toSearch() {
				uni.navigateTo({
					url: "/pages/search/search?"
				})

			},
			//轮播图跳转
			toSwiper(index) {
			    
                
				uni.navigateTo({
					url:"/pages/webview/webview?item="+index
				})
				
			},
			//分类跳转
			toCategory(e) {
				
				uni.navigateTo({
					url: "/pages/goods/goods-list?id=" + e.id + "&title=" + e.title
				})
			},
			//收货跳转
			skipGetgoods() {
				uni.navigateTo({
					url: "/pages/getgoods/getgoods"
				})
			},
			//商品跳转
			toGoods(e) {
				uni.navigateTo({
					url: "/pages/goods/goods?id=" + e.id
				})
			},
			//限时商品跳转
			limitGoods(e){
				uni.navigateTo({
					url: "/pages/goods/goods?id=" + e.id+"&limit=1"
				})
			},
			//轮播图指示器
			swiperChange(event) {
				this.currentSwiper = event.detail.current;
			}
		}
	}
